import {typeOf} from "./object";

export const delay = <T>(dur: number = 0) => {
    return new Promise((resolve) => {
        setTimeout(resolve, dur)
    })
}

/**
 * 判断是否为日期对象
 * @author  韦胜健
 * @date    2019/11/5 20:38
 */
export const isDate = (val: any): val is Date => {
    return typeOf(val) === 'date'
}

/**
 * 判断是否为普通对象
 * @author  韦胜健
 * @date    2019/10/15 22:19
 */
export const isPlainObject = (val: any): val is Object => {
    return typeOf(val) === 'object'
}

/**
 * 转义特殊字符
 * @author  韦胜健
 * @date    2019/11/5 20:40
 */
export const encode = (val: string): string => {
    return encodeURIComponent(val)
        .replace(/%40/g, '@')
        .replace(/%3A/ig, ':')
        .replace(/%24/g, '$')
        .replace(/%2C/ig, ',')
        .replace(/%20/g, '+')
        .replace(/%5B/ig, '[')
        .replace(/%5D/ig, ']')
}

/**
 * 节流
 * @author  韦胜健
 * @date    2019/11/16 18:49
 * @param   func                //核心函数
 * @param   wait                //节流时长（该时间段内只执行一次func）
 * @param   options             //额外参数
 * @param   options.trailing    //最后一次应该被触发，默认为true
 * @param   options.leading     //第一次是否立即执行
 */
export const throttle = <T extends Function>(func: T, wait: number, options: { trailing?: boolean, leading?: boolean } = {}): T => {
    let args: any, context: any, previous = 0, timeout: any;
    let later = () => {
        // console.log('later click：' + Date.now() % 10000)
        previous = options.leading === false ? 0 : Date.now()
        func.apply(context, args)
        args = context = timeout = null
    }

    return function () {
        args = arguments
        // @ts-ignore
        context = this
        let now = Date.now()

        if (!previous && options.leading === false) {
            previous = now
        }

        let remaining = wait - (now - previous)

        if (remaining <= 0) {
            /*第一次*/
            if (timeout) {
                clearTimeout(timeout)
                timeout = null
            }
            // console.log('throttle click：' + Date.now() % 10000)
            func.apply(context, args)
            previous = now

        } else if (!timeout && options.trailing !== false) {
            timeout = setTimeout(later, remaining)
        }
    } as any
}

/**
 * 防抖
 * @author  韦胜健
 * @date    2019/11/16 19:31
 */
export const debounce = <T extends Function>(func: T, wait: number, immediate?: boolean): T => {
    let timeout: any

    return function (...args: any[]) {
        // @ts-ignore
        const context = this
        clearTimeout(timeout)
        if (immediate) {
            let callNow = !timeout
            // @ts-ignore
            if (callNow) func.apply(this, args)
        }
        timeout = setTimeout(() => {
            func.apply(context, args)
            timeout = null
        }, wait)
    } as any
}

/**
 * 判断是否为ie，是的话，返回ie的版本
 * @author  韦胜健
 * @date    2020/7/27 22:49
 */
export const ie = (() => {
    let userAgent = navigator.userAgent; //取得浏览器的userAgent字符串
    let isIE = userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1; //判断是否IE<11浏览器
    let isEdge = userAgent.indexOf("Edge") > -1 && !isIE; //判断是否IE的Edge浏览器
    let isIE11 = userAgent.indexOf('Trident') > -1 && userAgent.indexOf("rv:11.0") > -1;
    if (isIE) {
        let reIE = new RegExp("MSIE (\\d+\\.\\d+);");
        reIE.test(userAgent);
        let fIEVersion = parseFloat(RegExp["$1"]);
        if (fIEVersion === 7) {
            return 7;
        } else if (fIEVersion === 8) {
            return 8;
        } else if (fIEVersion === 9) {
            return 9;
        } else if (fIEVersion === 10) {
            return 10;
        } else {
            return 6;//IE版本<=7
        }
    } else if (isEdge) {
        return 'edge';//edge
    } else if (isIE11) {
        return 11; //IE11
    } else {
        return false;//不是ie浏览器
    }
})()