/**
 * 驼峰命名转横岗命名
 * @author  韦胜健
 * @date    2019/10/28 20:15
 */
import {encode, isDate, isPlainObject} from "./utils";

export const kebabCase = (str: string | null): string | null => {
    if (!str) return null
    if (str.length > 1 && /[A-Z]/.test(str.charAt(0))) {
        str = str.charAt(0).toLowerCase() + str.substring(1)
    }
    return str.replace(/[A-Z]/g, (i) => '-' + i.toLowerCase())
}

/**
 * 转为驼峰命名
 * @author  韦胜健
 * @date    2019/10/28 20:17
 */
export const camelCase = (str: string | null): string | null => {
    if (!str) return null
    const SPECIAL_CHARS_REGEXP = /([:\-_]+(.))/g;
    const MOZ_HACK_REGEXP = /^moz([A-Z])/;
    str = str
        .replace(SPECIAL_CHARS_REGEXP, function (_, separator, letter, offset) {
            return offset ? letter.toUpperCase() : letter;
        })
        .replace(MOZ_HACK_REGEXP, 'Moz$1');

    if (str.length > 1 && /[a-z]/.test(str.charAt(0))) {
        return str.charAt(0).toUpperCase() + str.substring(1)
    } else {
        return str
    }
}

/**
 * 填充0字符
 * @author  韦胜健
 * @date    2019/10/28 20:40
 */
export const zeroize = (value: string | number, length = 2): string => {

    if (value == null) {
        value = ''
    }

    value = String(value)
    const zeroLen = length - value.length
    if (zeroLen <= 0) return value

    return new Array(zeroLen).fill('0').join('') + value
}

/**
 * 移除字符单位
 * @author  韦胜健
 * @date    2019/11/5 20:20
 */
export const removeUnit = (data: string | number | undefined | null, unit = 'px'): string => {
    if (!data) return ''
    data = String(data)
    if (data.indexOf(unit) > -1) {
        return data.replace(unit, '')
    } else {
        return data
    }
}

/**
 * 去掉px字符
 * @author  韦胜健
 * @date    2019/10/28 20:50
 */
export const removePx = (str: string | number | null | string): string | null | undefined => {
    return removeUnit(str, 'px')
}

/**
 * 获取唯一标识符
 * @author 韦胜健
 * @date 2018/11/19
 */
export const uuid = (): string => {
    let s: string[] = [];
    let hexDigits = '0123456789abcdef';
    for (let i = 0; i < 36; i++) {
        s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
    }
    s[14] = '4';                                        // bits 12-15 of the time_hi_and_version field to 0010
    s[19] = hexDigits.substr((Number(s[19]) & 0x3) | 0x8, 1);   // bits 6-7 of the clock_seq_hi_and_reserved to 01
    s[8] = s[13] = s[18] = s[23] = '-';

    let uuid = s.join('');
    return uuid;
}

/**
 * 添加字符单位
 * @author  韦胜健
 * @date    2019/1/10 10:56
 */
export const unit = (data: string | number | undefined | null, unit = 'px'): string => {
    if (!data) return ''
    data = String(data)
    if (data.indexOf(unit) > -1) {
        return data
    } else {
        return `${data}${unit}`
    }
}

export const suffixPx = (val: any): string | undefined => {
    if (val == null) return undefined
    if (/^[\d]+$/.test(String(val))) {
        return `${val}px`
    }
    return val as string
}

/**
 * 根据传入的参数params，生成url
 * @author  韦胜健
 * @date    2019/11/5 20:40
 */
export const encodeUrl = (url: string, params: object) => {
    if (!params) return url

    const parts: string[] = []
    Object.keys(params).forEach(key => {
        const val = params[key] as any
        if (val === null || typeof val === 'undefined') {
            return
        }
        let values = [] as any[]
        if (Array.isArray(val)) {
            values = val
            key += '[]'
        } else {
            values = [val]
        }

        values.forEach(val => {
            if (isDate(val)) {
                val = val.toISOString()
            } else if (isPlainObject(val)) {
                val = JSON.stringify(val)
            }
            parts.push(`${encode(key)}=${encode(val)}`)
        })
    })

    let serializedParams = parts.join('&')
    if (serializedParams) {
        const markIndex = url.indexOf('#')
        if (markIndex !== -1) url = url.slice(0, markIndex)
        url += (url.indexOf('?') === -1 ? '?' : '&') + serializedParams
    }

    return url
}

/**
 * 解析url，得到纯净的url，params参数以及哈希值
 * @author  韦胜健
 * @date    2019/11/5 20:52
 */
export const decodeUrl = (url: string): null | { url: string, params: object, hash: string } => {
    if (!url) return null

    let parts = url.split(/[?#]/)

    if (parts.length === 0) {
        console.error('地址解析失败！url=' + url)
        return null
    }

    let params = {} as any
    if (parts.length >= 2) {
        const paramParts = parts[1].split('&')
        paramParts.forEach((item: string) => {
            if (!item) return
            const [key, value] = item.split('=')
            params[decodeURIComponent(key)] = decodeURIComponent(value)
        })
    }

    return {
        url: parts[0] as string,
        params,
        hash: parts[2] as string
    }
}
